.card {
    min-height: 300px;
    padding: 10px;
    width: calc(100% / 4);
    flex-shrink: 0;
    &:hover {
      background: $hover-color;
    }
    // overflow: hidden;
    .card-img {
      position: relative;
      height: 40%;
      width: 100%;
      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
      .duration{
        position: absolute;
        bottom: 0;
        right: 0;
        margin: 5px;
        padding: 2px 5px;
        background: $primary-color;
      }
    }
    .card-body {
      .heading {
        padding: 10px 0;
      }
      .sub-details {
        color: gray;
        font-size: 14px;
        display: flex;
        .user-details {
          .video-details {
            display: flex;
            .video-time{
              text-transform: capitalize;
            }
            .video-views {
              margin-right: 10px;
              text-transform: uppercase;
            }
          }
        }
        .profile-img {
          height: 40px;
          width: 40px;
          margin-right: 10px;
          border-radius: 9999px;
          overflow: hidden;
          img {
            height: 100%;
            width: 100%;
            object-fit: cover;
          }
        }
      }
    }
  }