.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    .container {
      border: 1px solid $hover-color;
      padding: 40px;
  
      .heading {
        font-size: 60px;
        font-weight: 600;
      }
      .methods {
        padding: 40px 0;
        .method {
          padding: 20px;
          border: 1px solid $hover-color;
          display: flex;
          align-items: center;
          cursor: pointer;
          &:hover {
            background: $hover-color;
          }
          .iconify {
            font-size: 30px;
            margin-right: 10px;
          }
        }
      }
    }
  }