.sidebar {
    position: sticky;
    top: 10vh;
    left: 0;
    height: 90vh;
    width: 250px;
    background: $secondary-color;
    .uls {
      ul {
        padding: 10px 0;
        border-bottom: 1px solid $hover-color;
        .link:hover,
        .active {
          background: $hover-color;
        }
        .link {
          display: flex;
          align-items: center;
          padding: 10px 40px;
          cursor: pointer;
  
          .iconify {
            font-size: 24px;
            margin-right: 20px;
          }
        }
      }
    }
  }