.watch-screen{
    width: calc(100vw - 250px);
    padding: 10px ;
    .container{
      .other-video-section{
        width: 100%;
      }
      .video-section{
        margin-bottom: 30px;
        width: 80%;
        .video-player{
          height: 500px;
          width: 100%;
          background: #000;
        }
        .video-details{
          .title{
            margin: 10px 0;
          }
          .bottom{
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: lightgray;
            font-size: 16px;
            .views-time{
              display: flex;
              align-items: center;
              .views{
                text-transform: uppercase;
                margin-right: 20px;
              }
            }
            .like-share{
              display: flex;
              align-items: center;
              .like{
                display: flex;
                align-items: center;
                margin-right: 20px;
                .iconify{
                  font-size: 20px;
                }
              }
            }
          }
        }
        .channel-details{
          border-top: 1px solid $hover-color;
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-top: 10px;
          padding-top: 10px;
          .left{
            display: flex;
            align-items: center;
            .icon{
              height: 40px;
              width: 40px;
              margin-right: 10px;
              img{
                height: 100%;
                width: 100%;
                object-fit: cover;
                border-radius: 99999px;
              }
            }
          }
        }
      }
    } 
  }