nav.navbar {
    height: 10vh;
    width: 100%;
    top: 0;
    left: 0;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 40px;
    background: $secondary-color;
    z-index: 999;
    .logo {
      font-size: 30px;
      font-weight: bold;
      width: 25% ;
    }
    .search {
      width: 50% ;
      display: flex;
      align-items: center;
      background: $primary-color;
      color: $color;
      input::placeholder {
        color: $color;
      }
      input {
        padding: 5px 20px;
        width: 90%;
        color: $color;
        background: none;
      }
      button {
        padding: 5px 0;
        width: 10%;
        height: 100%;
        background: $hover-color;
        color: gray;
        .iconify {
          font-size: 30px;
        }
      }
    }
    .user{
      width: 25%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      .profile-img{
        height: 40px;
        width: 40px;
        img{
          border-radius: 999999px;
          height: 100%;
          width: 100%;
          object-fit: cover;
        }
      }
    }
  }