.card2 {
    display: flex;
    height: 220px;
    width: 100%;
    padding: 10px;
    margin: 10px;
    &:hover {
      background: $hover-color;
    }
    .img {
      height: 100%;
      width: 70%;
      margin-right: 20px;
      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }
    .body {
      color: lightgray;
      .heading {
        color: white;
        font-size: 18px;
      }
      .video-details {
        display: flex;
        margin-bottom: 10px;
        font-size: 14px;
        .views {
          margin-right: 10px;
        }
      }
      .desc {
        font-size: 14px;
      }
      .user-details {
        display: flex;
        margin-bottom: 10px;
        align-items: center;
        .img {
          height: 40px;
          width: 40px;
          margin-right: 10px;
          img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            border-radius: 99999px;
          }
        }
      }
    }
  }